$screen-mobile: 320px;
$screen-tablet: 768px;
$screen-notebook: 1024px;
$screen-desktop: 1200px;
$screen-max: 1600px;

$color-primary: #313237;
$color-secondary: #89939a;
$color-icons: #b4bdc4;
$color-elements: #e2e6e9;
$color-hover-bg: #fafbfc;
$color-white: #fff;
$color-green: #27ae60;
$color-red: #eb5757;

@font-face {
  font-family: "Mont";
  src: url("./fonts/Mont-Regular.otf") format("otf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Mont";
  src: url("./fonts/Mont-SemiBold.otf") format("otf");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Mont";
  src: url("./fonts/Mont-Bold.otf") format("otf");
  font-weight: 600;
  font-style: normal;
}

@mixin hoverAndFocus($_heightLine, $_beforeColor, $_afterColor) {
  position: relative;
  transition: all 0.6s;
  outline: none;

  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 0;
    height: $_heightLine;
    background: $_beforeColor;
    transition: all 0.3s;
  }

  &:hover,
  &:focus {
    &:before {
      left: 0;
      width: 100%;
      background: $_afterColor;
    }
  }
}

%h1 {
  font-size: 32px;
  font-weight: 600;
  line-height: 41px;
  letter-spacing: -0.01em;
}

%h2 {
  font-size: 22px;
  font-weight: 600;
  line-height: 31px;
  letter-spacing: 0;
}

%h3 {
  font-size: 20px;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: 0;
}

%uppercase {
  font-size: 12px;
  font-weight: 600;
  line-height: 11px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

%buttons {
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0;
}

%body {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0;
}

%small {
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  letter-spacing: 0;
}

%visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

html,
body,
ul,
h1,
h2,
h3,
li {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

html {
  font-size: 16px;
  font-family: "Mont", sans-serif;
  color: #313237;
}

body {
  @extend %body;
}

h1 {
  @extend %h1;
}

h2 {
  @extend %h2;
}

h3 {
  @extend %h3;
}

.container {
  max-width: $screen-max;
  margin: 0 auto;
  padding: 0 1rem;
}

.section {
  margin-bottom: 2rem;
}

.uppercase {
  @extend %uppercase;
}

.small-text {
  @extend %small;
}



.Button {
  @extend %buttons;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s, box-shadow 0.3s;

  &__primary {
    width: 11rem;
    height: 2.5rem;
    box-sizing: border-box;
    color: #fff;
    background-color: #313237;
    border: 1px solid #313237;

    &--selected {
      color: #27ae60;
      background-color: #fff;
      border-color: #e2e6e9;
    }

    &:hover,
    &:focus {
      box-shadow:0 3px 13px rgba(23, 32, 49, 0.26);
    }
  }

  &__pagination {
    height: 2rem;
    min-width: 2rem;
    background-color: #fff;
    border: 1px solid #b4bdc4;

    &--selected {
      color: #fff;
      background-color: #313237;
      border-color: #313237;
    }

    &:hover,
    &:focus {
      border-color: #313237;
    }

    &:disabled,
    &[disabled] {
      background-color: #fff;
      border-color: #e2e6e9;
      cursor: default;

      & .icon__image {
        background-color: #e2e6e9;
      }
    }
  }

  &__squared {
    height: 4rem;
    min-width: 4rem;
    background-color: #fff;
    border: 1px solid #b4bdc4;

    &--selected {
      border-color: #e2e6e9;
    }

    &:hover,
    &:focus {
      border-color: #313237;
    }
  }

  &__link {
    width: max-content;
    height: 2.5rem;
    text-decoration: none;
    color: #89939a;

    &::before {
      content: "";
      display: inline-block;
      height: 1rem;
      width: 0.5rem;
      margin-right: 0.5rem;
      mask-image: url(./images/icons/chevron-arrow-left.svg);
      mask-position: center;
      mask-repeat: no-repeat;
      background-color: #313237;
      transition: transform 1s cubic-bezier(0.86, 0, 0.07, 1);
    }

    &:hover,
    &:focus {
      color: #313237;

      &::before {
        content: "";
        transform: translateX(-0.25rem);
      }
    }
  }
}

.Color {
  &__label {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    box-sizing: border-box;
    padding: 1px;
    border: 1px solid #e2e6e9;
    border-radius: 50%;
    transition: border-color 0.3s;
    cursor: pointer;
    outline: none;

    &::after {
      content: "";
      display: block;
      height: 100%;
      border-radius: 50%;
    }

    &:hover,
    &:focus {
      border-color: #b4bdc4;
    }

    &--selected {
      border-color: #313237;

      &:hover,
      &:focus {
        border-color: #313237;
      }
    }

    &--green {
      &::after {
        content: "";
        background-color: #27ae60;
      }
    }

    &--red {
      &::after {
        content: "";
        background-color: #eb5757;
      }
    }
  }

  &__radio {
    @extend %visually-hidden;
  }
}

.Select {
  margin-bottom: 250px;
  cursor: pointer;

  &__active {
    position: relative;
    padding: 10px 0 9px 12px;
    width: 176px;
    height: 40px;
    border: 1px solid #b4bdc4;
    box-sizing: border-box;
    color: #313237;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    transition: all 0.3s;
    &--opened {
      border: 1px solid #313237;
    }
  }

  &__arrow {
    position: absolute;
    right: 15.33px;
    top: 50%;
    transform: translate(0, -50%);
    transition: 0.3s;
    transform-origin: center;

    &--opened {
      top: 30%;
      transform: rotate(-180deg);
    }
  }

  &__list {
    width: 176px;
    background: #fff;
    border: 1px solid #e2e6e9;
    box-sizing: border-box;
    box-shadow:0 2px 15px rgba(0, 0, 0, 0.05);
    padding: 8px 0;
    transform: translateY(-50px), scale(-0.5, 1);
    transition: all 0.5s;
    visibility: hidden;
    opacity: 0;

    &--opened {
      visibility: visible;
      transform: translateY(4px);
      opacity: 1;
    }
  }

  &__item {
    list-style-type: none;
    height: 32px;
    line-height: 32px;
    padding: 6px 0 5px 12px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    color: #89939a;
    transition: all 0.3s;

    &:hover {
      background: #fafbfc;
      color: #313237;
    }
  }
}

.Pagination {
  display: flex;
  justify-items: center;

  &__Button {
    margin: 0 0.25rem;
  }
}
